html {
    background-color: #17171d;
}

h1 {
    color: rgb(186, 191, 196);
    margin: 24px 0 8px 30px;
}

h3 {
    color: rgb(186, 191, 196);
    margin-left: 12px;
}

iframe {
    width: calc(100% - 40px);
    height: 321px;
    border: 0;
    margin: 20px 20px 20px;
    border-radius: 15px;
}

.bottom-button {
    margin-top: 12px !important;
}

.all-form-inputs {
    min-width: 180px !important;
    margin-left: 30px !important;
    margin-right: 30px !important;
    margin-bottom: 12px !important;
}


#full-screen {
    width: calc(100% - 40px);
    margin: 20px;
    color: rgb(186, 191, 196)
}

.big-container {
    border: 1px solid #323238;
    margin-right: 20px;
    margin-left: 20px;
    margin-bottom: 20px;
    border-radius: 12px;
}

.s-container {
    border-radius: 15px;
    border: 1px solid #31366d;
    width: fit-content;
    display: inline-block;
    margin: 12px;
    padding: 15px 15px 3px;
}

.music-text {
    margin-right: 20px;
    margin-left: 20px;
    position: relative;
    top: 6px;
}

.react-share__ShareButton {
    margin: 6px;
}

@media (max-width: 635px) {

    .bottom-button {
        width: 100%;
    }

    label {
        width: 100%;
    }

    .all-form-inputs {
        width: calc(100vw - 220px);
    }

    h1 {
        text-align: center;
    }

}